import Vue from 'vue'
import App from './App.vue'
import router from './router'
import'mint-ui/lib/style.css'//引入mint-ui组件样式
import MintUI from "mint-ui";//引入完整mint-ui组件 可以在任意组件使用
Vue.use(MintUI)//把MinUI 创建MintUI
Vue.config.productionTip = false
//5: 设置请求的根路径 
//Vue.http.options.root = "http://127.0.0.1/vue_ser/";
//6:全局设置post 时候表音的数据组织格式为 application/x-www-form-urlencoded
//Vue.http.options.emulateJSON = true;
// 导入 MUI 的样式表， 和 Bootstrap 用法没有差别
import './lib/mui/css/mui.css'
import './lib/mui/css/swiper.min.css'
import './lib/mui/css/mui.min.css'
// import './lib/mui/js/swiper.min.js'
// 导入 MUI 的样式表，扩展图标样式，购物车图标
// 还需要加载图标字体文件
import './lib/mui/css/icons-extra.css'
import axios from "axios"
//让axios携带钥匙进入数据库 //2.配置跨域选项
axios.defaults.withCredentials=true;
//给Vue的原型对象强行添加axios属性 axios配置Vue实例中 //3.将axios配Vue实例中
Vue.prototype.axios=axios;

//element
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
Vue.use(ElementUI)

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
