                    this.scenics=res;
                })
            },
            hosts(index){
                var res = this.$refs.index;
               for(var item of res){
                    item.style=""
               }
               var  i = document.querySelectorAll("li")[4];
               if(res[index]==undefined){
                   i.style="background:#4CB7F9"
               }else{
                res[index].style="background:#4CB7F9";
               }

               if(index==undefined){
                this.cut = this.cuts[0]
               }else if(index==0){
                    this.cut = this.cuts[0]
                }else if(index==1){
                    this.cut = this.cuts[1]
                }else if(index==2){
                    this.cut = this.cuts[2]
                }else if(index==3){
                    this.cut = this.cuts[3]
                }
