                }
                this.item()
            })
          },
          search(){
              var name=this.value;
              var url="Sserch?name="+name;
              this.axios.get(url).then(result=>{
                  if(result.data.code==-1){
                   this.$toast("请输入正确的城市名")
                    return;
                  }
                  var res=result.data.data[0];
                    this.id=res.c_id
                    this.load();
              })
          },
          router(){
                this.$router.push("/Home");
            },
        },
    }
