<template>
    <div>
        <div>
            <router-link to="/home" style="color:#666;font-size:12px">
                返回
            </router-link>
        </div>
       
        <!-- 分享动态 -->
        <h4 style="text-align: center;color:#1b7fb6;">说出我们的故事</h4>
        <div  v-for="(item,i) of img" :key="i" ref="item">
           <h1 style="color:black;font-size: 10px" v-text="item.content"></h1>
           <div style="width:100%;text-align: center">
                <img :src="'http://127.0.0.1:3000'+item.img" alt="" style="width:200px;"> 
                <button @click="Delelte"  style="border:0px;background:transparent;" :data-id="item.id">x</button>
           </div> 
           <!-- 点赞 -->
           <a href="#" @click="Addbtn">
               <span class="mui-icon-extra mui-icon-extra-like" v-model="number" style="font-size:12px;color: #999">
                   {{number}}
               </span>
            </a>
        </div>
        <hr>
        <!-- 查询生成 -->
       
        <!-- 分享 -->
        <div>
            <div style="margin-top:20px;">
                <form action="Social" method="POST" enctype="multipart/form-data">
                    <input type="text" name="name" class="playSee">
                    <p class="Spans" >图片
                        <input type="file" name="mypic" class="WenJian" style="opacity: 0">
                    </p>
                    <p @click="Shuax">
                        <input type="submit" value="发布" style="background:#ff8140;border:0px;float:left;margin-left:72%">
                    </p>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                img:[],
                number:0
            }
        },
        created() {
            this.load();  
        },
        methods: {
            Shuax(){
                location.reload()
            },
            Addbtn(){
                this.number++;
                if(this.number == 2){
                    this.$toast("你已经点过了");
                    this.number  = 1;
                }
            },
           
            Delelte(e){
               this.$messagebox.confirm("是否要删除此条动态").then(action=>{
                   console.log("删除")
                  var id = e.target.dataset.id
                   var url = "delete?id="+id;
                   this.axios(url).then(result=>{
                       console.log(result)
                       if(result.data.code == 1){
                           setInterval(()=>{
                            this.$router.go(0)
                            //刷新当前页面
                          setInterval(()=>{
                            this.$toast("删除成功");
                          },3000)
                           },100)
                       }
                   })
               }).catch(result=>{
                   console.log("取消")
               });
            },
              load(){
                  var url = "content";
                  this.axios(url).then(result=>{
                      console.log(result)
                      console.log(result.data.data)
                      this.img = result.data.data
                  })
              }  
            }
    }
</script>

<style scoped>
   .Shuax{
       display:inline-block
   }
    .mui-icon-extra-like:hover{
        color:brown
    }
    .Spans{
        background:url(http://127.0.0.1:3000/http://127.0.0.1:3000/img/zhanguanzhanlan.png);
        display:block;
        width:49px;
        height:37px;
        background-size:40px;
        padding-left:7px;
        color:black;
        float:left;
    }
    .WenJian{
        color:brown;
        width:85px;
    }
    .playSee{
        width:100%;
        height:100px;
        border-radius: 10px;
        font-size: 10px;
    }
</style>
