<template> 
    <div>
       <!-- <div>
            <el-upload
                class="avatar-uploader"
                 :action="Action" 
                  :show-file-list="false"
                 :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
       </div>
        -->
         <!-- 搜索 -->
      <div style="width:100%;height:40px;">
          <input type="text" placeholder="大家都在搜...." v-model="count" style="position:relative;width:80%;" class="InPut"> 
          <button @click="Serch" style="position: absolute;width:20%;height:6%;background:rgb(255, 129, 64);color:#fff">搜索</button>
          <!-- 查询结果 -->
          <div> 
            <p v-if="Image.length>0? true:false" style="color:black;font-size: 15px;text-align: center">与{{count}}相关动态</p>
            <div v-for="(item,i) of Image" style="width:100%;">
                <h3 v-text="item.content" style="font-size: 10px"></h3>
                <img :src="''+item.img" alt="" style="width:200px">
            </div>
        </div>
      </div>
      <!--  -->
      <h4 style="text-align: center;color:#1b7fb6;" v-if="img.length>0? 'true':'false'">看看我们的故事</h4>
        <div  v-for="(item,i) of img" :key="i" ref="item">
           <h1 style="color:black;font-size: 10px" v-text="item.content"></h1>
           <div style="width:100%;text-align: center">
                <img :src="''+item.img" alt="" style="width:200px;"> 
           </div> 
           <!-- 点赞 -->
           <a href="#" @click="Addbtn">
               <span class="mui-icon-extra mui-icon-extra-like" v-model="number" style="font-size:12px;color: #999">
                   {{number}}
               </span>
            </a>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
      return {
        imageUrl: '',
        Action:"http://127.0.0.1:3000/Shang",
        count:"",
        Image:[],
        img:[],
        number:0
      };
    },
    created() {
        console.log(this.imageUrl)
        this.load()
    },   
    methods: {
      Addbtn(){
                this.number++;
                if(this.number == 2){
                    this.$toast("你已经点过了");
                    this.number  = 1;
                }
            },
      load(){
          var url = "content";
                this.axios(url).then(result=>{
                      console.log(result)
                      console.log(result.data.data)
                      this.img = result.data.data
                  })
              }  ,
      Serch(){
                console.log(this.count)
                var url = "Serch?count="+this.count;
                this.axios.get(url).then(result=>{
                    console.log(result);
                    if(result.data.data.length == 0){
                        this.$toast("暂时没有数据哦")
                    }else if(result.data.data.length>0){
                        this.Image = result.data.data;
                        this.img = ""
                    }
                })
            },
      handleAvatarSuccess(res, file) {
       
          console.log(res,file)
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      }
    }
}
</script>

<style scoped>
     .InPut{
        color:#808080;
        font-size:12px;
        border:0;
    }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 100%;
    border-radius: 50%;
    display: block;
  }
</style>