<template>
    <div>
        <router-link to="/Cate">
            <i class="el-icon-arrow-left" style="font-size: 10px">返回</i>
        </router-link> 
        <ul style="list-style: none">
            <h5 v-text="`你好!  你今天预购${title.length}个餐台`"></h5>
            <li v-for="(item,i) of title">
                <p v-text="`时间为${item.title}`"></p>
            </li>
        </ul>
    </div>

</template>

<script>
    export default {
        data(){
            return {
                title:[]
            }
        },
        created() {
            this.load()
        },
        methods: {
            load(){
                var url = "Sorder";
                this.axios.get(url).then((result)=>{
                    console.log(result)
                    this.title =  result.data.msg
                    console.log(result.data.msg)
                })
            }
        },
    }
</script>

<style scoped> 
    a{
        text-decoration: none;
        display:block;
        background: #fff
    }
</style>